import {Observable} from "rxjs";
import {IAuth} from "../../models/auth.model";
import {Injectable} from "@angular/core";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {ILeadDetail, ILeadReq} from "../../../../../dash/domains/sales/leads/models/lead.model";
import {SalesEndpoints} from "../../../../../dash/domains/sales/sales.endpoints";
import {ResponseData} from "../../../../../../base/models/IPage";
import {ICampaignLeadReq} from "./campaign-lead.model";
import {BaseService} from "../../../../../../base/services/base.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {AuthService} from "../../services/auth.service";

@Injectable({
  providedIn: 'root'
})
export class PreRegistrationService extends BaseService{
  constructor(
    private http: HttpClient,
    private eventBusService: EventBusService,
    notifierService: NotifierService,
    authService: AuthService
  ) {
    super(authService, notifierService);
  }
  preRegister(key: string, endpoint: string, leadReq: ICampaignLeadReq) {
    let url = SalesEndpoints.LEADS.CREATE(endpoint)

    let observable = this.http.post<ResponseData<ILeadDetail>>(url, leadReq)

    observable.subscribe(res => {
      this.eventBusService.publish(key, res.body)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.preRegister(key, endpoint, leadReq)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }
}
