import {Injectable} from '@angular/core';
import {BaseService} from "../../../../../base/services/base.service";
import {AuthService} from "../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {IParams} from "../../../../../base/endpoints/iparams";
import {IPage, ResponseData} from "../../../../../base/models/IPage";
import {IOrganization, IOrganizationDetail, IOrganizationReq} from "../models/organization.model";
import {IAuth} from "../../../../common/modules/auth/models/auth.model";
import {DashEndpoint} from "../../../dash.endpoint";

@Injectable({
  providedIn: 'root'
})
export class OrganizationService extends BaseService {

  constructor(
    authService: AuthService,
    notifier: NotifierService,
    private http: HttpClient,
    private eventBusService: EventBusService
  ) {
    super(authService, notifier)
  }

  search(key: string, endpoint: string, params: IParams) {
    let url = DashEndpoint.ORGANIZATIONS.SEARCH(endpoint, params)
    this.http.get<ResponseData<IOrganization>>(url)
      .subscribe((organization: ResponseData<IOrganization>) => {
        this.eventBusService.publish(key, organization.body)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.search(key, endpoint, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchSingle(key: string, endpoint: string, id: number) {
    this.http.get<ResponseData<IOrganization>>(DashEndpoint.ORGANIZATIONS.GET(endpoint, id))
      .subscribe((organization: ResponseData<IOrganization>) => {
        this.eventBusService.publish(key, organization.body)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchSingle(key, endpoint, id)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchMyOrgs(key: string) {
    this.http.get<ResponseData<IOrganization[]>>(DashEndpoint.ORGANIZATIONS.GET_MY_ORGS())
      .subscribe((organization: ResponseData<IOrganization[]>) => {
        this.eventBusService.publish(key, organization.body)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchMyOrgs(key)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  upsert(key: string, organizationReq: IOrganizationReq, exId: number | null) {
    let endpoint = exId == null ? DashEndpoint.ORGANIZATIONS.CREATE() : DashEndpoint.ORGANIZATIONS.UPDATE(exId)
    let observable = exId == null ? this.http.post<ResponseData<IOrganizationDetail>>(endpoint, organizationReq)
      : this.http.patch<ResponseData<IOrganizationDetail>>(endpoint, organizationReq)
    observable.subscribe(res => {
      this.eventBusService.publish(key, res.body)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.upsert(key, organizationReq, exId)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }

}
