import {Layout} from "../../../generic/breadcrumb/breadcrumb.model";
import {DashEndpoint} from "../../../../../dash/dash.endpoint";
import {BaseConfig} from "../../../../../../base/models/baseConfig";


export interface FileConfig extends BaseConfig {
  imageEndpoint: string
  imgDelEndpoint: string
}

export let defaultFileConfig: FileConfig = {
  defaultLayout: Layout.GRID,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: true,
  searchEndpoint: DashEndpoint.FILES.FILE_ENDPOINT,
  imageEndpoint: DashEndpoint.FILES.IMAGE_ENDPOINT,
  delEndpoint: DashEndpoint.FILES.FILE_ENDPOINT,
  imgDelEndpoint: DashEndpoint.FILES.IMAGE_ENDPOINT
}


export let selfRoutes = {
  list: "/cms/files",
  create: "/cms/files/create",
  update(id: number | null) {
    return id == null ? this.create : "/dash/files/" + id + '/update'
  }
}
