import {Component, ElementRef, Inject, LOCALE_ID, Renderer2, ViewChild} from '@angular/core';
import {BaseComponent} from "../../../../base/components/base.component";
import {EventBusService} from "../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router, UrlTree} from "@angular/router";
import {SubscriptionContainerService} from "../../../../base/services/subscription-container.service";
import {AuthService} from "../../../common/modules/auth/services/auth.service";
import {IAuth} from "../../../common/modules/auth/models/auth.model";
import {DialogService} from "../../../common/modules/utility/components/popup/dialog.service";
import {LeaderboardComponent} from "../../../common/modules/accounting/leaderboard/leaderboard.component";
import {FormControl} from "@angular/forms";
import {Locales} from "../../models/global.config";
import {LocalStorageService} from "../../../../services/storage/localstorage.service";
import {Utility} from "../../../common/modules/utility/utility";
import {EventKey} from "../../../../services/eventbus/event.key";
import {IOrganization} from "../../domains/organizations/models/organization.model";
import {OrganizationService} from "../../domains/organizations/services/organization.service";

@Component({
  selector: 'app-dash-header',
  templateUrl: './dash-header.component.html',
  styleUrls: ['./dash-header.component.scss']
})
export class DashHeaderComponent extends BaseComponent {
  @ViewChild('rightBarToggle') rightBarToggle: ElementRef;

  protected readonly Locals = Locales;

  search = new FormControl()

  auth: IAuth
  isAdmin: boolean = false

  authorities: string[] = []
  orgs: IOrganization[] = []
  selectedOrg: IOrganization | null = null

  currentLocale: Locales = LocalStorageService.retrieve('locale') as Locales || Locales.EN_US

  protected componentName(): string {
    return DashHeaderComponent.name;
  }

  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private authService: AuthService,
    private renderer: Renderer2,
    private popupService: DialogService<null>,
    private activatedRoute: ActivatedRoute,
    private orgService: OrganizationService,
    @Inject(LOCALE_ID) protected locale: string
  ) {
    super(eventBus, router, subContainer)
    this.auth = this.authService.getAuth()
    this.isAdmin = this.authService.isAdmin()
    this.authorities = authService.getAuthorities()
  }

  hasPrivilege(privilege: string): boolean {
    return this.isAdmin || this.authorities.includes(privilege)
  }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      this.search.setValue(params['query'])
    })

    this.currentLocale = LocalStorageService.retrieve('locale') as Locales || Locales.EN_US

    this.resolveOrganizations()
  }

  private resolveOrganizations() {
    let selectedOrgStr: string | null = LocalStorageService.retrieve("selectedOrg") || null
    this.selectedOrg = selectedOrgStr != undefined ? JSON.parse(selectedOrgStr) : null

    this.fetchOrganizations(orgs => {
      this.orgs = orgs
      if (this.selectedOrg != undefined || orgs.length > 0) {
        this.onOrgSelected(orgs[0])
      }
      LocalStorageService.put("orgs", JSON.stringify(orgs))
    })

  }

  onOrgSelected(org: IOrganization) {
    this.selectedOrg = org
    LocalStorageService.put("selectedOrg", JSON.stringify(org))
  }

  protected fetchOrganizations(block: (orgs: IOrganization[]) => void) {
    // fetch My org key
    let key = this.componentKey(EventKey.ORGANIZATIONS.GET_MY_ORG())
    this.orgService.fetchMyOrgs(key)
    this.subscribe(key, null, (value, meta) => {
      let orgs = meta.data as IOrganization[]
      block(orgs)
    })
  }

  onSearch() {
    const urlTree: UrlTree = this.router.parseUrl(this.router.url);
    const path = urlTree.root.children['primary'].segments.map(segment => segment.path).join('/');

    this.router.navigate([path], {
      queryParams: {
        query: this.search.value,
      },
      queryParamsHandling: 'merge',
    }).then(r => {
    });
  }

  clearSearch() {
    this.search.setValue(null)
    this.onSearch()
  }

  logout() {
    this.authService.logout();
  }

  initLeftMenuCollapse() {
    const body = document.body;

    body.classList.toggle('sidebar-enable');
    if (window.innerWidth >= 992) {
      body.classList.toggle('vertical-collpsed');
    } else {
      body.classList.remove('vertical-collpsed');
    }

    if (body.classList.contains('vertical-collpsed')) {
      this.renderer.addClass(body, 'vertical-collpsed');
    } else {
      this.renderer.removeClass(body, 'vertical-collpsed');
    }
  }

  initRightSidebar() {
    // right side-bar toggle
    const body = document.querySelector('body');

    this.renderer.listen(body, 'click', (e: Event) => {
      const target = e.target as HTMLElement;

      if (target.closest('.right-bar-toggle, .right-bar')) {
        return;
      }

      this.renderer.removeClass(body, 'right-bar-enabled');
    });

    this.renderer.listen(document.querySelector('.right-bar-toggle'), 'click', () => {
      this.renderer.addClass(body, 'right-bar-enabled');
    });
  }

  openLeaderboard() {
    let popup = this.popupService.open(LeaderboardComponent, {id: null})
    let component = popup.componentInstance
    component.onCanceled.subscribe(() => {
      popup.close()
    })
  }

  setLocale(locale: Locales): void {
    LocalStorageService.put('locale', locale)

    this.currentLocale = locale;
    const currentUrl = this.router.url;
    let newUrl = currentUrl.replace(/\/[a-z]{2}-[A-Z]{2}\//, `/${locale}/`);
    if (!newUrl.startsWith('/' + locale + '/')) {
      newUrl = '/' + locale + '/#' + newUrl;
    }
    window.location.href = window.location.origin + newUrl;
  }

  protected readonly Utility = Utility;

  protected readonly Locales = Locales;
}
