export class Utility {
  static deepCopy(obj: any): any {
    if (obj === null || typeof obj !== 'object') {
      return obj;
    }

    const copy: any = Array.isArray(obj) ? [] : {};
    for (const key in obj) {
      if (Object.prototype.hasOwnProperty.call(obj, key)) {
        if (obj[key] instanceof Date) {
          copy[key] = new Date(obj[key]);
        } else {
          copy[key] = Utility.deepCopy(obj[key]);
        }
      }
    }
    return copy;
  }

  static isMobileScreen = window.innerWidth < 992
  static isTabletOrMobileScreen = window.innerWidth <= 1280
  static isTabletScreen = window.innerWidth <= 1280 && window.innerWidth >= 992

  static percentage(value: number, total: number): number {
    if (total === 0) return 0
    return (value / total) * 100
  }

  static randomInt(from: number, to: number) {
    return Math.floor(Math.random() * (to - from) + from)
  }

  static toFloat(value: number, fractionalDigits: number = 2): number {
    return parseFloat(value.toFixed(fractionalDigits))
  }
}
