export interface MenuItem {
  title: string;
  icon: string;
  link: string;
  selected: boolean;
  children: MenuItem[];
  permissions: string[];
}

enum Permissions {
  CAN_MANAGE_INVENTORY = "CAN_MANAGE_INVENTORY",
  CAN_ACCESS_CRM = "CAN_ACCESS_CRM",
  ADMINISTRATION = "ADMINISTRATION",
  ACCESS_USER_RESOURCES = "ACCESS_USER_RESOURCES",
  CAN_TAKE_EXAM = "CAN_TAKE_EXAM",
}

