import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {DashLayoutComponent} from "./modules/dash/layout/dash-layout/dash-layout.component";
import {AuthGuardService} from "./modules/common/modules/auth/services/auth-guard.service";
import {UserDeleteComponent} from "./modules/common/modules/auth/components/user-delete/user-delete.component";

const routes: Routes = [
  // {
  //   path: '',
  //   component: LayoutComponent,
  //   loadChildren: () => import("./modules/webapp/web-app.module").then(m => m.WebAppModule)
  // },
  {
    path: 'auth',
    loadChildren: () => import('./modules/common/modules/auth/auth.module').then(m => m.AuthModule)
  },
  {
    path: 'social',
    loadChildren: () => import('./modules/common/modules/social/social.module').then(m => m.SocialModule)
  },
  {
    path: '',
    component: DashLayoutComponent,
    loadChildren: () => import('./modules/dash/dash.module').then(m => m.DashModule),
    canActivate: [AuthGuardService]
  },
  {
    path: 'dash',
    component: DashLayoutComponent,
    loadChildren: () => import('./modules/dash/dash.module').then(m => m.DashModule),
    canActivate: [AuthGuardService]
  },
  {
    path: 'request-account-deletion',
    component: UserDeleteComponent
  },
  {
    path: 'cms',
    loadChildren: () => import('./modules/common/modules/cms/cms.module').then(m => m.CmsModule),
    component: DashLayoutComponent,
    canActivate: [AuthGuardService]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {scrollPositionRestoration: 'enabled'})],
  exports: [RouterModule]
})
export class AppRoute {
}
